/* mobile first style */
@keyframes slideLeftIn {
	0% {
		left: 0;
	}

	100% {
		left: 50vw;
	}
}

@keyframes slideLeftOut {
	0% {
		left: 50vw;
	}

	100% {
		left: 0;
	}
}

@keyframes navLeftIn {
	0% {
		left: -50vw;
	}

	100% {
		left: 0;
	}
}

@keyframes navLeftOut {
	0% {
		left: 0;
	}

	100% {
		left: -50vw;
	}
}

