@media all and (max-width:959px) {

	/* desktop excluded */
	.btn-menu {
		display: block;
		position: fixed;
		top: 20px;
		margin-left: 20px;
		width: 30px;
		height: 30px;
		border-radius: 50%;
		background: #1f1f1f;
	}

	.btn-menu:after {
		content: "arrow_forward";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		text-align: center;
		font-size: 25px;
		line-height: 30px;
		color: #fff;
		font-family: 'Material Icons';
		font-feature-settings: 'liga';
	}

	.base-body {
		width: 100%;
		position: relative;
		animation-name: slideLeftOut;
		animation-duration: .2s;
		animation-iteration-count: 1;
		animation-timing-function: ease-out;
		animation-fill-mode: forwards;
	}

	.nav-open .base-body {
		animation-name: slideLeftIn;
		animation-duration: .2s;
		animation-iteration-count: 1;
		animation-timing-function: ease-out;
		animation-fill-mode: forwards;
	}

	.header-wrap .logo {
		animation-name: slideLeftOut;
		animation-duration: .2s;
		animation-iteration-count: 1;
		animation-timing-function: ease-out;
		animation-fill-mode: forwards;
	}

	.nav-open .header-wrap .logo {
		animation-name: slideLeftIn;
		animation-duration: .2s;
		animation-iteration-count: 1;
		animation-timing-function: ease-out;
		animation-fill-mode: forwards;
	}

	.header-nav {
		animation-name: navLeftOut;
		animation-duration: .2s;
		animation-iteration-count: 1;
		animation-timing-function: ease-out;
		animation-fill-mode: forwards;
	}

	.nav-open .header-nav {
		display: block;
		animation-name: navLeftIn;
		animation-duration: .2s;
		animation-iteration-count: 1;
		animation-timing-function: ease-out;
		animation-fill-mode: forwards;
	}

	.btn-menu {
		animation-name: slideLeftOut;
		animation-duration: .2s;
		animation-iteration-count: 1;
		animation-timing-function: ease-out;
		animation-fill-mode: forwards;
	}

	.nav-open .btn-menu {
		animation-name: slideLeftIn;
		animation-duration: .2s;
		animation-iteration-count: 1;
		animation-timing-function: ease-out;
		animation-fill-mode: forwards;
	}

	.nav-open .btn-menu:after {
		content: "arrow_back";
	}
}

@media all and (min-width:960px) {

	/* desktop */
	body:after {
		content: "desktop";
		display: block;
		position: fixed;
		bottom: 0;
		right: 0;
		padding: 5px;
		background: #000;
		color: #fff;
		font-size: 12px;
		z-index: 1000;
		opacity: 0.5
	}

	/* layout */
	.base-header {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
		width: 260px;
		padding: 0;
		transition: height 0.3s, background-color 0.3s
	}

	.base-body {
		position: relative;
		width: 100%;
	}

	.base-aside {
		display: block;
		position: fixed;
		bottom: 30px;
		right: 0;
		width: 130px;
		padding: 0 10px;
		z-index: 1;
		border-left: 1px solid #e2e2e2
	}

	.base-main {
		position: relative;
		z-index: 0;
		width: 100%;
		padding: 0 150px 100px 260px;
		font-size: 14px;
	}

	.base-footer {
		padding: 30px 20px;
		text-align: center;
	}

	/* header */
	.header-wrap {
		width: 100%;
		padding: 0 40px;
		margin: 0 auto;
		font-size: 0;
		text-align: center;
		transition: margin 0.3s
	}

	.header-wrap .logo {
		display: block;
		width: 42px;
		padding: 50px 0 20px;
	}

	.st0 {
		fill: #FFFFFF;
		stroke: #000000;
		stroke-miterlimit: 10;
	}

	.btn-menu {
		display: none;
	}

	.header-nav {
		display: block;
		width: 100%;
		margin: 30px 0 0;
		position: relative;
		left: 0 !important;
		height: auto;
		background: transparent;
		padding: 0;
		overflow: visible;
	}



	/* common */
	.tit-catagory {
		display: none
	}

	.tit-wrap {
		padding: 140px 0 0 0;
	}

	.tit-wrap em {
		font-size: 12px;
		margin-top: 10px;
		color: #999
	}

	.tit-wrap .h-1 {
		outline: 0;
		display: inline-block;
		position: relative;
	}

	.tit-wrap .h-1:focus:after {
		content: "";
		position: absolute;
		left: 0;
		width: 100%;
		bottom: 18px;
		display: block;
		border-bottom: 1px dashed #000;
		opacity: 0.4;
	}


	.h-1 {
		font-size: 100px;
		margin: 0;
		line-height: 1.2;
	}

	.h-2 {
		font-size: 40px;
		margin: 50px 0 7px;
	}

	.h-3 {
		font-size: 25px;
		margin: 30px 0 7px;
	}

	.h-4 {
		font-size: 20px;
		margin: 10px 0 7px;
	}

	.h-5 {
		font-size: 18px;
		margin: 20px 0 5px;
	}

	.h-6 {
		font-size: 18px;
		margin: 20px 0 5px;
	}

	.type-zindex col.n1 {
		width: 140px;
	}

	.type-zindex col.n2 {
		width: 140px;
	}

	.type-zindex col.n3 {
		width: 140px;
	}

	.type-zindex col.n4 {
		width: auto;
	}

	.tbl-test1 col.n1 {
		width: 100px
	}

	.tbl-test1 col.n2 {
		width: auto
	}

	.tbl-test1 col.n3 {
		width: 200px
	}

	.tbl-test1 col.n4 {
		width: 150px
	}

	.tbl-test1 col.n5 {
		width: 150px
	}

	.type-discription .col-th {
		width: 140px;
	}

	.tab-test-scroll {
		width: 500px;
	}

}