/* ui slide */
.ui-slide {
    position: relative;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    margin: 10px 0;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.ui-slide-wrap {
    position: relative;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    -webkit-transition: height 0.3s;
    -moz-transition: height 0.3s;
    -o-transition: height 0.3s;
    transition: height 0.3s;
}

.ui-slide-itemwrap {
    position: relative;
    top: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
}

.ui-slide.load .ui-slide-item {
    opacity: 1;
    display: block;
}

.ui-slide-item {
    display: none;
    position: absolute;
    top: 0;
    opacity: 0;
    z-index: 0;
    cursor: move;
}

.ui-slide-item[aria-hidden="false"] {
    display: block;
    z-index: 5;
}

.ui-slide-tit {
    display: none;
    position: absolute;
    z-index: 10;
}

.ui-slide-dotwrap {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    text-align: center;
}

.ui-slide-navwrap button {
    position: absolute;
    top: 50%;
    z-index: 10;
}

.ui-slide-navwrap .ui-slide-next {
    right: 0;
}

.ui-slide-navwrap .ui-slide-prev {
    left: 0;
}

.ui-slide-dot[aria-selected="true"] {
    font-weight: 700;
}

.ui-slide-gauge {
    position: absolute;
    width: 100%;
    height: 5px;
    background-color: #ccc;
}

.ui-slide-gaugebar {
    position: absolute;
    top: 0;
    width: 0;
    height: 100%;
    background-color: red;
}

/* ui slide multi */
.ui-slide-multi .ui-slide-itemwrap {
    width: auto;
    overflow: hidden;
    cursor: move;
}

.ui-slide-multi .ui-slide-item {
    float: left;
    position: relative;
    left: 0 !important;
}


/* ui slider */
.ui-slider {
    position: relative;
    width: 100%;
    padding: 50px 50px 0;
}

.ui-slider-wrap {
    position: relative;
    width: 100%;
    padding: 5px 0;
    z-index: 1;
}

.ui-slider-bg {
    position: relative;
    width: 100%;
    background-color: #ccc;
    height: 10px;
    border-radius: 5px;
}

.ui-slider-bar {
    position: absolute;
    width: 0;
    height: 100%;
    top: 0;
    left: 50%;
    background: #303030;
    border-radius: 5px;
    z-index: 2;
    transition: all 0.2s;
}

[class^="ui-slider-btn"] {
    position: absolute;
    top: 0;
    background: #fff;
    border: 1px solid #303030;
    width: 20px;
    height: 20px;
    border-radius: 10px;
    z-index: 3;
    overflow: hidden;
    color: transparent;
    font-size: xx-small;
    transition: left 0.2s;
    margin-left: -10px;
}

[class^="ui-slider-btn"]:focus,
[class^="ui-slider-btn"].on {
    z-index: 4;
}

[class^="ui-slider-btn"] span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.ui-slider-divwrap {
    position: relative;
    z-index: 0;
    width: 100%;
    height: 30px;
}

.ui-slider-div {
    position: absolute;
    bottom: 0;
    padding: 0;
    z-index: 1;
    display: block;
    height: 30px;
}

.ui-slider-div em {
    display: block;
    text-align: center;
    width: 100%;
    position: absolute;
    top: 10px;
    box-sizing: border-box;
    white-space: nowrap;
    font-size: 11px
}

.ui-slider-div:after {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    display: block;
    width: 1px;
    height: 10px;
    border-left: 1px solid #ccc;
}

.ui-slider-tooltip {
    position: absolute;
    top: -40px;
    overflow: hidden;
    text-align: center;
    border: 1px solid #303030;
    background: #fff;
    padding: 5px 10px;
    z-index: 1;
    white-space: nowrap;
    transition: all 0.2s;
}

/* ui slider type :reverse, vertical */
.type-reverse .ui-slider-bar {
    left: auto
}

.type-reverse [class^="ui-slider-btn"] {
    margin-right: -10px;
    margin-left: 0
}

.type-vertical {
    width: 100px;
    height: 300px;
    padding: 20px
}

.type-vertical .ui-slider-wrap {
    width: 20px;
    height: 100%;
    padding: 0 5px;
}

.type-vertical .ui-slider-bg {
    width: 10px;
    height: 100%;
}

.type-vertical .ui-slider-bar {
    width: 10px;
    height: 0;
    top: auto;
    left: 0;
}

.type-vertical [class^="ui-slider-btn"] {
    top: auto;
    left: 0;
    margin: -10px 0 0;
}

.type-vertical .ui-slider-divwrap {
    position: absolute;
    top: 0;
    left: 15px;
    width: auto;
    height: 100%;
}

.type-vertical .ui-slider-div {
    width: 100%;
    padding-left: 15px;
    bottom: auto
}

.type-vertical .ui-slider-div:after {
    top: 50%;
    left: 0;
    width: 10px;
    height: 1px;
    border-top: 1px solid #ccc;
    border-left: 0
}

.type-vertical .ui-slider-div em {
    text-align: left;
    top: 5px
}

.type-vertical .ui-slider-tooltip {
    left: auto;
    right: 20px
}

.type-vertical.type-reverse .ui-slider-btn-s,
.type-vertical.type-reverse .ui-slider-btn-e {
    margin: 0 0 -10px;
}

.type-vertical.type-reverse .ui-slider-tooltip {
    top: auto
}

/* ui slider mobile */
.ui-m .ui-slider-div {
    font-size: 11px;
}

.ui-m .ui-slider-.n0 em {
    padding-left: 0;
    text-align: center;
}

.ui-m .ui-slider-.n10 em {
    padding-right: 0;
    text-align: center;
}


/* ui error */
.ui-error-msg {
    width: 100%;
    display: none;
    font-size: 11px !important;
    color: orangered;
    margin-top: 5px
}

.ui-error-msg:before {
    content: "!";
    display: inline-block;
    margin-right: 3px
}

.ui-error-true .ui-error-msg {
    display: block;
}

.ui-error-true .ui-error-item,
.ui-error-true .ui-error-select .ui-select-btn {
    border-color: orangered
}
